html {
  font-size: 16px; }

body {
  box-sizing: border-box;
  color: #fff;
  font-size: 1rem;
  font-family: 'Lato', sans-serif; }

* {
  padding: 0;
  margin: 0; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #015b6d;
  color: #a0bdc5;
  padding: 0 10rem; }
  .header__title {
    display: flex;
    align-items: center; }
    .header__title .heading {
      display: inline-block;
      margin: 0;
      font-size: 2rem; }
  @media screen and (min-width: 640px) and (max-width: 900px) {
    .header {
      padding: 0;
      display: block; }
      .header__title {
        justify-content: center; } }
  @media screen and (max-width: 640px) {
    .header {
      padding: 0;
      display: block; }
      .header__title {
        justify-content: center; } }

.search-container {
  width: 20rem;
  position: absolute;
  left: 50%;
  top: -50%;
  transform: translateX(-50%); }
  .search-container input {
    width: 100%;
    border: 0.2rem;
    background-color: #307685;
    color: #9fbec6;
    padding: 1rem; }
    .search-container input::placeholder {
      color: #9fbec6; }
    .search-container input:focus {
      outline: none; }
  .search-container .suggestionItem {
    cursor: pointer;
    text-align: left;
    padding: 1rem;
    background-color: #307685;
    color: #9fbec6;
    border-top: 1px solid #9fbec6; }

@media screen and (min-width: 1080px) and (max-width: 1215px) {
  .search-container {
    top: -43.7%; } }

@media screen and (min-width: 920px) and (max-width: 1079px) {
  .search-container {
    top: -36.1%; } }

@media screen and (max-width: 919px) {
  .search-container {
    top: -26.8%; } }

.forecastItem {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 0.2rem;
  text-align: center;
  padding: 1rem 3rem; }
  .forecastItem .name {
    font-size: 1.1rem; }
  .forecastItem .temp {
    font-size: 2rem; }
  @media screen and (min-width: 1080px) and (max-width: 1215px) {
    .forecastItem {
      flex: 0 30%;
      margin: 1rem; } }
  @media screen and (min-width: 920px) and (max-width: 1079px) {
    .forecastItem {
      flex: 0 46%;
      margin: 1rem; } }
  @media screen and (max-width: 919px) {
    .forecastItem {
      flex: 0 100%;
      margin: 1rem; } }

.current-weather {
  grid-area: current-weather; }
  .current-weather .city {
    font-size: 1.5rem;
    margin-bottom: 1.2rem; }
  .current-weather .temp {
    font-size: 2.2rem; }
  @media screen and (max-width: 1215px) {
    .current-weather {
      justify-self: center; } }

.favorites-controller {
  grid-area: favorites-controller;
  justify-self: end; }
  .favorites-controller svg {
    color: #fff;
    font-size: 3rem;
    margin-right: 1rem; }
  .favorites-controller .btn {
    border-radius: 0.2rem;
    font-weight: bold;
    padding: 0.5rem 0.6rem; }
    .favorites-controller .btn-add {
      background: transparent;
      border: 4px solid #fff;
      color: #fff; }
    .favorites-controller .btn-remove {
      background: #fff;
      color: #307685; }
  @media screen and (max-width: 1215px) {
    .favorites-controller {
      justify-self: center;
      align-self: center; } }

.favoritesItem {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 0.2rem;
  text-align: center;
  padding: 1rem 3rem;
  cursor: pointer;
  margin: 1rem; }
  @media screen and (min-width: 1070px) and (max-width: 1350px) {
    .favoritesItem {
      flex: 0 30%; } }
  @media screen and (min-width: 1016px) and (max-width: 1069px) {
    .favoritesItem {
      flex: 0 46%; } }
  @media screen and (max-width: 1015px) {
    .favoritesItem {
      flex: 0 100%; } }

.main .hero-image {
  background-image: linear-gradient(to right, rgba(9, 138, 156, 0.4), rgba(98, 189, 190, 0.4)), url(../../../images/city.jpg);
  background-position: center;
  height: 20rem;
  width: 100%; }

.home-page {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 12rem);
  grid-template-areas: "current-weather . favorites-controller" "forecast-container forecast-container forecast-container";
  padding: 6rem; }
  @media screen and (max-width: 1215px) {
    .home-page {
      padding: 3rem;
      grid-template-rows: 9rem 9rem auto;
      grid-template-areas: "current-weather current-weather current-weather" "favorites-controller favorites-controller favorites-controller" "forecast-container forecast-container forecast-container"; } }

.favoritesPage {
  display: flex;
  flex-wrap: wrap;
  padding: 6rem; }

.forecast-container {
  grid-area: forecast-container;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }
  @media screen and (max-width: 1215px) {
    .forecast-container {
      justify-content: left; } }

.toggle-container {
  color: #a0bdc5; }
  .toggle-container .btn {
    color: #a0bdc5;
    margin-right: 1rem; }
    .toggle-container .btn--active {
      border: 2px solid #a0bdc5;
      border-radius: 0.2rem;
      color: #a0bdc5;
      font-weight: bold; }
  @media screen and (min-width: 640px) and (max-width: 900px) {
    .toggle-container {
      display: flex; }
      .toggle-container .btn {
        margin: 0;
        flex: 1 50%; }
        .toggle-container .btn--active {
          background: #a0bdc5;
          border-radius: 0;
          color: #015b6d;
          font-weight: bold; } }
  @media screen and (max-width: 640px) {
    .toggle-container .btn {
      margin: 0;
      display: block;
      flex: 1 100%; }
      .toggle-container .btn--active {
        background: #a0bdc5;
        border-radius: 0;
        color: #015b6d;
        font-weight: bold; } }

.app {
  min-height: 100vh;
  background-image: linear-gradient(to bottom right, #098a9c, #339eac, #43abb1, #62bdbe); }
